        this.provinsi.label = item.label;
      } else if (input == "kota") {
        this.kota.label = item.label;
      } else if (input == "kel") {
        this.kel.label = item.label;
      } else if (input == "kode") {
        this.kode.label = item.label;
      } else if (input == "address") {
        this.address.label = item.label;
      } else if (input == "milik") {
        this.milik.label = item.label;
      } else if (input == "cicil") {
        this.cicil.label = item.label;
      }
    },
    check_credit(e) {
      if (e.target.value.length < 16) {
        this.error_mes_cc = "Nomor kartu kredit harus 16 digit";
      } else {
        this.error_mes_cc = "";
      }
    },
    onFileChange(e) {
      let el_id = e.target.id;
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0], el_id);
    },
    createImage(file, id) {
      let reader = new FileReader();
      let vm = this;
      reader.onload = e => {
        vm.image[id] = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage(id) {
      this.image[id] = "";
    }
  }
};
</script>
